.main{
    display: flex;
    justify-content: flex-start;
    flex-direction: column;
    align-items: flex-start;
    box-sizing: border-box;
    text-align: left;
}

.main > *{
    margin: 1rem 0;
    margin-top: 0;
    width: 100%;
    padding: 0 2rem;
    box-sizing: border-box;
}

.main > *:first-child{
    margin-top: 1rem;
}

.variable{
    margin-bottom: 3rem;
}