
.inputPanel, .main{
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
    color: var(--secondary-color);
}

.main{
    width: 100%;
    height: 100%;
}

.inputPanel{
    flex: 1;
    flex-direction: column;
}

.varsPanel{
    min-height: 100%;
    width: 0rem;
    transition: width .5s;
    position: relative;
    box-sizing: border-box;
    background-color: var(--primary-color);
}

.varsPanel::before{
    content: "";
    width: 1px;
    height: 100%;
    background-color: var(--secondary-color);
    position: absolute;
    right: -1px;
}

.varsPanel.varsActive{
    width: min(35rem, 500px);
}

.varsPanel .cut{
    display: block;
    position: absolute;
    width: 100%;
    height: 100%;
    overflow: hidden;
}

.varsPanel .container{
    width: min(35rem, 500px);
    min-height: 100%;
    left: 0;
    box-sizing: border-box;
}

.varsPanel{
    content: "";
}

.varsPanelTab{
    position: absolute;
    top: 40%;
    height: 20%;
    width: 40px;
    border: 1px var(--secondary-color) solid;
    border-left: 1px var(--secondary-color) dotted;
    background-color: var(--primary-color);
    right: -40px;
    color: var(--primary-textColor);
}

.inputPanel h1{
    margin: 1em 0 .25em 0;
}

.inputPanel h1+div{
    font-size: 1.5rem;
}

.error{
    margin: 1em 0 .25em 0;
    color: var(--error-textColor);
}

.errorSub{
    color: var(--error-textColor);
    font-weight: 400;
}