* {
  border: 0;
  margin: 0;
  color: inherit;
}

:root {
  --primary-color: #1c1c21;
  --secondary-color: #6EDFF6;
  --secondary-color-50a: #52738d;
  --opacity-placeholder: .55;
  --opacity-placeholder-active: .40;

  --primary-textColor: white;
  --error-textColor: rgb(246, 110, 110);
  --secondary--textColor: rgba(110,223,246);

  --opacity-button: .75;
  --opacity-button-active: .65;
  --opacity-button-disabled: .55;

  font-size: min(1vw, 22px);
}

body{
  font-size: 15px;
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

button{
  user-select: none;
  cursor: pointer;
}

.App {
  width: 100vw;
  height: 100vh;
  background-color: var(--primary-color);
}