.main {
    height: 2.375em;
    padding: 0em .625em;
    background-color: var(--primary-color);
    color: var(--secondary-color);
    border-width: .125em;
    border-style: solid;
    border-color: var(--secondary-color);
    border-radius: .625em;
    outline: 0;
    font-size: 1em;
    font-family: "Open Sans";
}

.main::placeholder {
    color: var(--secondary-color);
    opacity: var(--opacity-placeholder);
}

.main:focus::placeholder {
    color: var(--secondary-color);
    opacity: var(--opacity-placeholder-active);
}