         url('./OpenSans/open-sans-v27-latin-300.eot?#iefix') format('embedded-opentype'), /* IE6-IE8 */
         url('./OpenSans/open-sans-v27-latin-300.woff2') format('woff2'), /* Super Modern Browsers */
         url('./OpenSans/open-sans-v27-latin-300.woff') format('woff'), /* Modern Browsers */
         url('./OpenSans/open-sans-v27-latin-300.ttf') format('truetype'), /* Safari, Android, iOS */
         url('./OpenSans/open-sans-v27-latin-300.svg#OpenSans') format('svg'); /* Legacy iOS */
  }
