.customComboBox, .customTextField{
    border-width: .15em!important;
    border-radius: 0;
    border-color: var(--secondary-color-50a);
    border-style: none;
    border-bottom-style: solid;
    background: radial-gradient(ellipse 100% 80% at bottom, rgba(110,223,246,.2) 0%, rgba(0,0,0,0) 60%);
    
}

.customComboBox:focus, .customTextField:focus{
    background: radial-gradient(ellipse 100% 100% at bottom, rgba(110,223,246,.2) 0%, rgba(0,0,0,0) 70%);
    border-color: var(--secondary-color);
}

.main{
    display: flex;
    flex-direction: column;
}

.varDefArea, .row{
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
}

.varDefArea > *, .row > *{
    margin-left: .5em;
    margin-right: .5em;
}

.varDefArea > *:first-child, .row > *:first-child{
    margin-left: 0;
}

.varDefArea > *:last-child, .row > *:last-child{
    margin-right: 0;
}