.main{
    height: 3.7rem;
    margin: 0 1rem;
    padding: 0rem 1rem;
    background-color: var(--secondary-color);
    color: var(--primary-color);
    border-width: .2rem;
    border-style: solid;
    border-color: var(--secondary-color);
    border-radius: 1rem;
    outline: 0;
    font-size: 1.6rem;
    font-weight: 600;
    font-family: "Open Sans";
    cursor: pointer;
}

.main:hover{
    opacity: var(--opacity-button);
}

.main:active{
    opacity: var(--opacity-button-active);
}

.main:disabled{
    opacity: var(--opacity-button-disabled);
}