@import url(../../fonts/Montserrat.css);
@import url(../../fonts/OpenSans.css);

.main {
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
    color: var(--secondary-color);
}

.main > * > h1 {
    font-family: 'Montserrat';
    font-size: 7em;
    font-weight: 400;
}

.main > * > p {
    font-family: 'Open Sans';
    font-size: 1em;
    font-weight: 400;
}

.pBar {
    width: 40vw;
    -webkit-appearance: none;
    appearance: none;
    height: 2px;
    background-color: var(--secondary-color-50a);
    box-shadow: 1px 1px 4px rgba(255, 255, 255, 0.1);
}

.pBar > *{
    background-color: var(--secondary-color);
    height: 100%;
    transition: .5s;
}

.pTxt {
    font-family: 'Open Sans';
    font-size: .75em;
    color: var(--secondary-color);
}

.pCont{
    height: 0;
    transition: .5s;
    overflow: hidden;
    display: flex;
    justify-content: space-around;
    flex-direction: column;
    align-items: center;
    margin: 0;
}

.loaded .pCont{
    margin: .5em 0;
    height: calc(2.5em);
}